  content: "";
  position: absolute;
  display: inline-block;
  height: 14px;
  width: 14px;
  left: 0;
  top: 0;
  background-image: url(data:image/svg+xml;base64,);
  background-size: 14px 14px;
  cursor: pointer;
  vertical-align: middle;
}
input[type=radio]:disabled:checked::after {
  background-image: url(data:image/svg+xml;base64,);
  content: "";
  position: absolute;
  display: inline-block;
  background-size: 14px 14px;
  height: 14px;
  width: 14px;
  left: 0;
